.layouts-container{
    width: 100%;
    height: 60vh;
    display: flex;
    justify-content: center;
}
.layouts-card{
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
}
.card{
    display: flex;
    padding: 0px 25px 0px 0px;
    background-color: #ebebeb;
    border-radius: 15px;
    box-shadow: 0px 4px 32px 0px rgba(0, 0, 0, 0.25);
}
.card-img-container{
    border-radius: 15px;
    box-shadow: 0px 4px 32px 0px rgba(0, 0, 0, 0.25);
}
.card-img{
    max-width: 400px;
    border-radius: 15px
}
.card-text{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin: 0px 55px;
}
.card-text-header{
    color: #EAB154;
    text-align: center;
}
.card-text-paragraph{
    display: flex;
    align-items: center;
    justify-content: center;
}
.card-text-paragraph:nth-child(2){
    padding-top: 30px;
    border-top: 1px solid var(--vazur);
}
.card-text-paragraph:nth-child(4){
    padding-bottom: 30px;
    border-bottom: 1px solid var(--vazur);
}

  .button:nth-child(n+2){
    margin-left: 20px;
  }

  