.video-container{
    position: relative;
    border-radius: 15px;
    height: 620px;
    width: 1100px;
    margin: 50px auto;
  }
  
  .video-container::before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(62, 122, 123, 0.5);
    padding: 0;
    margin: 0;
    border-radius: 15px;
  }
  
  .video{
    width: 100%;
    height: auto;
    border-radius: 15px;
  }
  
  .cutaway-text-container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
    text-align: center;
    color: white;
    width: 80%;
  }
  
  .cutaway-text-header {
    color: var(--gold);
    font-size: 3rem;
    margin-bottom: 2rem;
  }
  
  .cutaway-text-paragraph {
    font-size: 1.5rem;
    display: block;
    margin-bottom: 4rem;
  }
  .button-container{
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  .play-button {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 2;
  }
  
  